import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(name, empId, email) {
  return { name, empId, email };
}

const rows = [
  createData('TeacherName1', 'EMPIDXXX6', 'tname1@example.com'),
  createData('TeacherName2', 'EMPIDXXX7', 'tname2@example.com'),
  createData('TeacherName3', 'EMPIDXXX8', 'tname3@example.com'),
];

export default function TeacherTable() {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align='right'>EMP ID</TableCell>
            <TableCell align='right'> Email</TableCell>
            <TableCell align='right'> Edit</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.name}>
              <TableCell component='th' scope='row'>
                {row.name}
              </TableCell>
              <TableCell align='right'>{row.empId}</TableCell>
              <TableCell align='right'>{row.email}</TableCell>
              <TableCell align='right'>
                <Link to='/editTeacher'>Edit</Link>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
