import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);
function createData(usn, name) {
  return { usn, name };
}
const useStyles = makeStyles(theme => ({
  table: {
    minWidth: 60,
  },
  action: {
    marginRight: theme.spacing(2),
    flexGrow: 1,
    padding: 20,
  },
}));

const rows = [
  createData('17BTCS090', 'Kashyap Prashanth'),
  createData('17BTCS130', 'Akhil Srivastara'),
  createData('17BTCS0150', 'Rahul Sharma'),
];

export default function Tables() {
  const classes = useStyles();
  return (
    <div>
      <Grid
        container
        spacing={4}
        direction='row'
        justify='flex end'
        alignItems='center'
      >
        <Grid item>
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label='customized table'>
              <TableHead>
                <TableRow>
                  <StyledTableCell align='center'>USN</StyledTableCell>
                  <StyledTableCell align='center'>Name</StyledTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map(row => (
                  <StyledTableRow key={row.usn}>
                    <StyledTableCell component='th' scope='row'>
                      {row.usn}
                    </StyledTableCell>
                    <StyledTableCell align='center'>{row.name}</StyledTableCell>
                  </StyledTableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <Link to='/students'>
            <Grid container justify='flex-end' style={{ padding: 20 }}>
              <Button
                classname={classes.action}
                variant='contained'
                size='medium'
                color='primary'
                justify='center'
              >
                View all
              </Button>
            </Grid>
          </Link>
        </Grid>
      </Grid>
    </div>
  );
}
