import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import SideBar from '../components/SideBar';
import ClassTable from '../components/ClassTable';
import Images from '../components/Images';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';

const useStyles = makeStyles(theme => ({
  class: {
    width: 'auto',
    height: 'auto',
  },
  margin: {
    margin: theme.spacing(1),
  },
  img: {
    width: theme.spacing(500),
    height: theme.spacing(50),
  },
  action: {
    marginRight: theme.spacing(2),
    flexGrow: 1,
  },
  form: {
    margin: theme.spacing(2),
    width: 200,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export default function EditClasses() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    sem: '',
    branch: '',
    sec: '',
  });
  const [open, setOpen] = React.useState(false);

  const handleChange = event => {
    setState(event.target.value);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <div>
      <SideBar />

      <Grid
        container
        direction='column'
        justify='flex-start'
        alignItems='center'
        spacing={2}
        style={{
          minHeight: '75vh',
          marginTop: '3%',
          marginBottom: '3%',
        }}
      >
        <Card className={classes.class}>
          <Grid
            container
            direction='column'
            justify='flex-start'
            alignItems='center'
            spacing={2}
            style={{ padding: '10%' }}
          >
            <Grid item>Class Details</Grid>
            <Grid item>
              <TextField
                className={classes.margin}
                id='outlined-basic'
                variant='outlined'
                placeholder='Subject Name'
              />
            </Grid>
            <Grid item>
              <TextField
                className={classes.margin}
                id='outlined-basic'
                variant='outlined'
                placeholder='Class Id'
              />
            </Grid>
            <Grid item>
              <FormControl className={classes.formControl}>
                <InputLabel id='demo-controlled-open-select-label'>
                  Semester
                </InputLabel>
                <Select
                  labelId='demo-controlled-open-select-label'
                  id='demo-controlled-open-select'
                  open={open}
                  onClose={handleClose}
                  onOpen={handleOpen}
                  value={state.sem}
                  onChange={handleChange}
                >
                  <MenuItem value={1}>I</MenuItem>
                  <MenuItem value={2}>II</MenuItem>
                  <MenuItem value={3}>III</MenuItem>
                  <MenuItem value={4}>IV</MenuItem>
                  <MenuItem value={5}>V</MenuItem>
                  <MenuItem value={6}>VI</MenuItem>
                  <MenuItem value={7}>VII</MenuItem>
                  <MenuItem value={8}>VIII</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <FormControl className={classes.formControl}>
                <InputLabel id='demo-controlled-open-select-label'>
                  Branch
                </InputLabel>
                <Select
                  labelId='demo-controlled-open-select-label'
                  id='demo-controlled-open-select'
                  open={open}
                  onClose={handleClose}
                  onOpen={handleOpen}
                  value={state.branch}
                  onChange={handleChange}
                >
                  <MenuItem value=''>
                    <em>None</em>
                  </MenuItem>
                  <MenuItem value={1}>CSE</MenuItem>
                  <MenuItem value={1}>IT</MenuItem>
                  <MenuItem value={1}>ECE</MenuItem>
                  <MenuItem value={1}>MECH</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <FormControl className={classes.formControl}>
                <InputLabel id='demo-controlled-open-select-label'>
                  Section
                </InputLabel>
                <Select
                  labelId='demo'
                  id='demo'
                  open={open}
                  onClose={handleClose}
                  onOpen={handleOpen}
                  value={state.sec}
                  onChange={handleChange}
                >
                  <MenuItem value=''>
                    <em>None</em>
                  </MenuItem>
                  <MenuItem value={2}>A</MenuItem>
                  <MenuItem value={2}>B</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <Button
                className={classes.action}
                variant='contained'
                size='large'
                color='primary'
                justify='center'
              >
                Add Class
              </Button>
            </Grid>
          </Grid>
        </Card>
      </Grid>
    </div>
  );
}
