import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(semester, branch, section) {
  return { semester, branch, section };
}

const rows = [
  createData('VII', 'ECE', 'A'),
  createData('IV', 'CSE', 'B'),
  createData('III', 'MECH', 'A'),
];

export default function StudentTable() {
  const classes = useStyles();

  return (
    <div>
      <TableContainer component={Paper}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell align='left'>Semester</TableCell>
              <TableCell align='center'> Branch</TableCell>
              <TableCell align='center'> Section</TableCell>
              <TableCell align='center'> Edit</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => (
              <TableRow key={row.semester}>
                <TableCell component='th' scope='row'>
                  {row.semester}
                </TableCell>
                <TableCell align='center'>{row.branch}</TableCell>
                <TableCell align='center'>{row.section}</TableCell>
                <TableCell align='center'>
                  <Link to='/editClasses'>Edit</Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
