import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import CloudCircleRoundedIcon from '@material-ui/icons/CloudCircleRounded';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import InputAdornment from '@material-ui/core/InputAdornment';
import LockIcon from '@material-ui/icons/Lock';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import { sizing } from '@material-ui/system';
import AccountCircleRoundedIcon from '@material-ui/icons/AccountCircleRounded';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../actions/userActions';
import Paper from '@material-ui/core/Paper';
import InputCard from '../components/InputCard';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: '#e4f0e2',
  },
  card: {
    width: '100wh',
    height: '100vh',
  },
  margin: {
    margin: theme.spacing(1),
  },
  img: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
  action: {
    marginRight: theme.spacing(2),
    flexGrow: 1,
  },
  form: {
    margin: theme.spacing(2),
    width: '50%',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
}));

const AdminLogin = ({ location, history }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const dispatch = useDispatch();

  const userLogin = useSelector(state => state.userLogin);
  const { loading, error, userInfo } = userLogin;

  const redirect = location.search ? location.search.split('=')[1] : '/';

  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  const submitHandler = e => {
    e.preventDefault();
    dispatch(login(email, password));
  };

  const classes = useStyles();
  const theme = useTheme();
  const [state, setState] = React.useState({
    checkedA: true,
  });
  const handleChange = event => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };
  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
        direction='column'
        alignItems='center'
        justify='center'
        style={{ minHeight: '100vh' }}
      >
        <Grid item>
          <CloudCircleRoundedIcon className={classes.img} />
        </Grid>

        <Card className={classes.class}>
          <Grid
            container
            spacing={0}
            direction='column'
            alignItems='center'
            justify='center'
          >
            <form className={classes.form} noValidate autoComplete='off'>
              <Grid
                container
                direction='column'
                alignItems='center'
                justify='center'
                spacing={1}
              >
                <Grid item>
                  <Typography
                    variant='body1'
                    style={{
                      color: theme.palette.common.black,
                    }}
                  >
                    Welcome
                  </Typography>
                </Grid>
                <Grid item>
                  <CardContent>
                    <TextField
                      className={classes.margin}
                      id='standard-basic'
                      label='Email'
                      variant='outlined'
                      fullWidth
                    />
                    <TextField
                      className={classes.margin}
                      id='standard-password-input'
                      placeholder='Password'
                      label='Password'
                      type='password'
                      variant='outlined'
                      fullWidth
                    />
                  </CardContent>
                </Grid>
                <Grid item>
                  <CardActions>
                    <Link to='/dashboard' style={{ textDecoration: 'none' }}>
                      <Button
                        className={classes.action}
                        variant='contained'
                        size='large'
                        color='primary'
                        justify='center'
                      >
                        Log In
                      </Button>
                    </Link>
                  </CardActions>
                </Grid>
                <Grid>
                  <p>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={state.checkedA}
                          onChange={handleChange}
                          name='checkedA'
                          color='primary'
                        />
                      }
                      label={
                        <Typography
                          variant='body2'
                          style={{
                            color: theme.palette.common.black,
                          }}
                        >
                          Show Password
                        </Typography>
                      }
                    />
                  </p>
                </Grid>
                <Grid>
                  <p>
                    <Typography variant='body2'>
                      Forgot Password?{' '}
                      <a href='/alogin' style={{ textDecoration: 'none' }}>
                        Click here
                      </a>
                    </Typography>
                  </p>
                </Grid>
              </Grid>
            </form>
          </Grid>
        </Card>
      </Grid>
    </div>
  );
};

export default AdminLogin;
