import './App.css';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { ThemeProvider } from '@material-ui/styles';
import AdminLogin from './screens/AdminLogin';
import AdminDashboard from './screens/AdminDashboard';
import Students from './screens/Students';
import EditStudent from './screens/EditStudent';
import Teachers from './screens/Teachers';
import EditTeacher from './screens/EditTeacher';
import Classes from './screens/Classes';
import EditClasses from './screens/EditClasses';
import Homepage from './screens/Homepage';
import theme from './components/ui/Theme';

const App = () => {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Route path='/dashboard' component={Homepage} exact />
        <Route path='/' component={AdminLogin} exact />
        <Route path='/students' component={Students} exact />
        <Route path='/editstudent' component={EditStudent} exact />
        <Route path='/teachers' component={Teachers} exact />
        <Route path='/editTeacher' component={EditTeacher} exact />
        <Route path='/classes' component={Classes} exact />
        <Route path='/editClasses' component={EditClasses} exact />
      </Router>
    </ThemeProvider>
  );
};

export default App;
