import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import SideBar from '../components/SideBar';
import ClassTable from '../components/ClassTable';
import Images from '../components/Images';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';

const useStyles = makeStyles(theme => ({
  class: {
    maxWidth: 345,
  },
  margin: {
    margin: theme.spacing(1),
  },
  img: {
    width: theme.spacing(500),
    height: theme.spacing(50),
  },
  action: {
    marginRight: theme.spacing(2),
    flexGrow: 1,
  },
  form: {
    margin: theme.spacing(2),
    width: 200,
  },
}));

export default function EditStudent() {
  const classes = useStyles();
  return (
    <div>
      <Paper>
        <SideBar />
        <Grid
          container
          direction='row'
          justify='flex-start'
          alignItems='flex-start'
          style={{ minHeight: '75vh', marginTop: '3%', marginLeft: '3%' }}
        >
          <Grid item xs={6}>
            <Grid
              container
              direction='column'
              justify='flex-start'
              alignItems='flex-start'
              spacing={2}
              style={{ minHeight: '75vh' }}
            >
              <Grid item>Basic Details</Grid>
              <Grid item>
                <TextField
                  className={classes.margin}
                  id='outlined-basic'
                  variant='outlined'
                  placeholder='Name'
                />
              </Grid>
              <Grid item>
                <TextField
                  className={classes.margin}
                  id='outlined-basic'
                  variant='outlined'
                  placeholder='USN'
                />
              </Grid>
              <Grid item>
                <TextField
                  className={classes.margin}
                  id='outlined-basic'
                  variant='outlined'
                  placeholder='Email'
                />
              </Grid>
              <Grid item>
                <TextField
                  className={classes.margin}
                  id='outlined-basic'
                  variant='outlined'
                  placeholder='Email'
                />
              </Grid>
              <Grid item>
                <Grid
                  container
                  direction='row'
                  justify='space-between'
                  alignItems='flex-start'
                >
                  <Grid item>Classes enrolled</Grid>
                  <Grid item>
                    <Button color='default' component={Link} to='/editClasses'>
                      + Add Class
                    </Button>
                  </Grid>
                </Grid>
                <ClassTable />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={6}>
            <Grid
              container
              direction='column'
              justify='flex-start'
              alignItems='flex-start'
              spacing={2}
              style={{ minHeight: '75vh' }}
            >
              <Grid item> Add upto six images</Grid>
              <Grid item>
                <Images />
              </Grid>
              <Grid item>
                {' '}
                <TextField
                  disabled
                  id='outlined-basic'
                  variant='outlined'
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position='end'>
                        <Button
                          variant='contained'
                          size='large'
                          color='primary'
                        >
                          Choose File
                        </Button>
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>
              <Grid item>
                <Link to='/save'>
                  <Button
                    className={classes.action}
                    variant='contained'
                    size='large'
                    color='primary'
                    justify='center'
                  >
                    Save
                  </Button>
                </Link>
              </Grid>
              <Grid item>
                Note: The first picture will be the Display Picture
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
