import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Link } from 'react-router-dom';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(name, usn, email) {
  return { name, usn, email };
}

const rows = [
  createData('Name1', '17BTCSxx6', 'name1@example.com'),
  createData('Name2', '17BTCSxx7', 'name2@example.com'),
  createData('Name3', '17BTCSxx9', 'name3@example.com'),
];

export default function StudentTable() {
  const classes = useStyles();

  return (
    <div>
      <Paper>
        <TableContainer>
          <Table className={classes.table}>
            <TableHead>
              <TableRow>
                <TableCell>name</TableCell>
                <TableCell align='right'>USN</TableCell>
                <TableCell align='right'> Email</TableCell>
                <TableCell align='right'> Edit</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map(row => (
                <TableRow key={row.name}>
                  <TableCell component='th' scope='row'>
                    {row.name}
                  </TableCell>
                  <TableCell align='right'>{row.usn}</TableCell>
                  <TableCell align='right'>{row.email}</TableCell>
                  <TableCell align='right'>
                    <Link to='/editStudent'>Edit</Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </div>
  );
}
