import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import SideBar from '../components/SideBar';
import Typography from '@material-ui/core/Typography';
import DateFnsUtils from '@date-io/date-fns';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Microphone from '../data/microphone.svg';

import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers';

const useStyles = makeStyles(theme => ({
  mainContainer: {
    marginTop: '2em',
    [theme.breakpoints.down('md')]: {
      marginTop: '3em',
    },
    [theme.breakpoints.down('xs')]: {
      marginTop: '2em',
    },
    backgroundColor: theme.palette.common.orange,
  },
  heroTextContainer: {
    minWidth: '21.5em',
    marginLeft: '1em',
    [theme.breakpoints.down('xs')]: {
      marginLeft: 0,
    },
  },
  class: { marginTop: '1em', marginBottom: '1em' },
  microphone: {
    backgroundImage: `url(${Microphone})`,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    height: '50%',
    width: '50%',
  },
}));
export default function Homepage() {
  const classes = useStyles();
  const theme = useTheme();
  const [selectedDate, setSelectedDate] = React.useState(
    new Date('2020-08-18T21:11:54')
  );

  const handleDateChange = date => {
    setSelectedDate(date);
  };
  return (
    <Paper>
      <SideBar />
      <Grid container direction='column' className={classes.mainContainer}>
        <Grid item>
          <Grid
            container
            justify='space-evenly'
            alignItems='flex-start'
            direction='row'
            spacing={1}
          >
            <Grid item className={classes.heroTextContainer}>
              <Typography variant='h2' align='center'>
                <Card className={classes.class}>
                  <Grid
                    container
                    spacing={0}
                    direction='column'
                    alignItems='flex-start'
                    justify='center'
                  >
                    <Grid item>
                      <CardMedia
                        component='img'
                        alt='Microphone'
                        height='200'
                        width='40'
                        image={Microphone}
                        title='Contemplative Reptile'
                        classname={classes.microphone}
                      />
                      <CardContent>
                        <Typography gutterBottom variant='h5' component='h2'>
                          Announcements
                        </Typography>
                        <Typography
                          variant='body2'
                          color='textSecondary'
                          component='p'
                        >
                          Today's announcements for you.
                        </Typography>
                      </CardContent>
                      <CardActions>
                        <Button size='small' color='primary'>
                          View All
                        </Button>
                      </CardActions>
                    </Grid>
                  </Grid>
                </Card>
              </Typography>
            </Grid>
            <Grid item className={classes.class}>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  height={'100%'}
                  width={'100%'}
                  disableToolbar
                  variant='inline'
                  format='MM/dd/yyyy'
                  label='Date planner'
                  margin='normal'
                  id='date-picker-inline'
                  value={selectedDate}
                  onChange={handleDateChange}
                  KeyboardButtonProps={{
                    'aria-label': 'change date',
                  }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
}
